<template>
    <yd-layout>
        <div class="mainContentFluid partOne">
            <div class="confirTop">
                <div class="advice">
                    <div class="confirmTrip">
                        请确认是否按计划请假：
                    </div>
                    <div class="selectAdvice">
                        <span class="selectBtn rSpace" v-bind:class="{selectActive:confirmNum==1}" v-on:click="confirmNum=1">是</span>
                        <span class="selectBtn" v-bind:class="{selectActive:confirmNum==2}" v-on:click="confirmNum=2">否</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="mainContentFluid partOne">
            <div class="realTime">
                <yd-cell-group>
                    <yd-cell-item>
                        <span slot="left" class="confirmTitle">实际休假开始时间：</span>
                        <yd-datetime :class="'dateColor'" type="date" v-model="realStartDate" slot="right"></yd-datetime>
                    </yd-cell-item>
                    <yd-cell-item>
                        <span slot="left" class="confirmTitle">实际休假结束时间：</span>
                        <yd-datetime :class="'dateColor'" type="date" v-model="realEndDate" slot="right"></yd-datetime>
                    </yd-cell-item>
                    <yd-cell-group class="cellGrop">
                        <yd-cell-item>
                            <span slot="left" :class="'confirmTitle'">实际休假天数：</span>
                            <yd-input slot="right" class="dateInput" v-model="realDays"></yd-input>
                        </yd-cell-item>
                    </yd-cell-group>
                </yd-cell-group>
            </div>
        </div>
        <div class="mainContent">
            <div class="contentSradius holiday">
                <div class="holiday_two">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="hint">
                                    {{formData.appLyName}}休假申请
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="leftWord unit">
                                    请假类型：
                                </div>
                                <div class="rightWord unitColor">年休假</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="leftWord">
                                    开始时间：
                                </div>
                                <div class="rightWord">{{startDate}}</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="leftWord">
                                    结束时间：
                                </div>
                                <div class="rightWord">{{endDate}}</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="leftWord">
                                    休假天数：
                                </div>
                                <div class="rightWord">{{days}}天</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.isOut!=''&&(formData.applyLevel=='2'||formData.orgname=='总经理室')">
                            <yd-flexbox>
                                <div class="leftWord">
                                    出国（境）：
                                </div>
                                <div class="rightWord" v-if="formData.isOut=='1'">是</div>
                                <div class="rightWord" v-if="formData.isOut=='0'">否</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.outDate!=''&&(formData.applyLevel=='2'||formData.orgname=='总经理室')">
                            <yd-flexbox>
                                <div class="leftWord">
                                    离境时间：
                                </div>
                                <div class="rightWord" v-text="formatDate(formData.outDate, 'yyyy-MM-dd')"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.enterDate!=''&&(formData.applyLevel=='2'||formData.orgname=='总经理室')">
                            <yd-flexbox>
                                <div class="leftWord">
                                    入境时间：
                                </div>
                                <div class="rightWord" v-text="formatDate(formData.enterDate, 'yyyy-MM-dd')"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.agentname!=''">
                            <yd-flexbox>
                                <div class="leftWord">
                                    工作代理人：
                                </div>
                                <div class="rightWord" v-text="formData.agentname"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.address!=''&&(formData.applyLevel=='1'||formData.applyLevel=='2'||formData.orgname=='总经理室')">
                            <yd-flexbox>
                                <div class="leftWord">
                                    请假地点：
                                </div>
                                <div class="rightWord" v-text="formData.address"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.remark!=''">
                            <!-- <div class="tripRemark">请假事由：</div>
                            <div class="tripReason" v-html="formData.remark">
                            </div> -->
                            <yd-flexbox>
                                <div class="leftWord remarkLeft">
                                    请假事由：
                                </div>
                                <div class="rightWord remarkRight" v-html="formData.remark"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContentFluid">
            <div class="historyWidth">
                <div class="approHistory">
                    <div class="title">
                        审批记录
                    </div>
                    <div class="noRecord" v-show="aproveList.length==0">暂无审批记录</div>
                    <div v-for="(item, index) in aproveList" :key="index" v-show="aproveList.length>0">
                        <yd-flexbox>
                            <div class="approName" v-if="item.activityDefName!='领导秘书预审'">{{item.partiname}}：</div>
                            <!-- <div class="approName" v-if="item.activityDefName=='领导秘书预审'">&nbsp&nbsp&nbsp：</div> -->
                            <yd-flexbox-item class="neirongSize" v-html="transferString(item.neirong)"></yd-flexbox-item>
                        </yd-flexbox>
                        <yd-flexbox>
                            <div class="approTime">{{formatDate(item.submitDate, 'yyyy-MM-dd')}}</div>
                            <div class="approTime">{{formatDate(item.submitDate, 'hh:mm')}}</div>
                        </yd-flexbox>
                    </div>
                </div>
            </div>
        </div>
        <!-- <div class="select_leader" v-if="slFlag">
            <select-Leader :holidayTime="holidayTime" :approveType="approveType" :approver="approver" @botSetflag="setFlag"></select-Leader>
        </div> -->
        <yd-tabbar slot="tabbar" v-if="status == 0 && userInfo.role!=2 && confirmNum == 2">
            <div class="assistBottom">
                <div class="agentLeader">
                    <yd-flexbox>
                        <div class="examineLogo_txt01" v-on:click="toSelectLeader()">送领导审批</div>
                    </yd-flexbox>
                </div>
                <div class="agentLeader">
                    <yd-flexbox>
                        <div class="examineLogo_txt02" v-on:click="confHoliday()">确认销假</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-show="status == 0 && userInfo.role!=2 && confirmNum == 1">
            <div class="footer">
                <yd-button v-on:click.native="confHoliday()" size="large" type="primary" bgcolor="#8ece18" color="#fefefe">确认销假</yd-button>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-show="status == 0 && userInfo.role==2">
            <div class="footer">
                <yd-button v-on:click.native="confHoliday()" size="large" type="primary" bgcolor="#8ece18" color="#fefefe">确认销假</yd-button>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'confirmTrip',
    data() {
        return {
            remark: '',
            formData: [],
            startDate: '',
            endDate: '',
            realStartDate: '',
            realEndDate: '',
            realDays: '',
            days: '',
            aproveList: [],
            approver: [],
            checkNum: 999,
            leaderIDs: '',
            queryTypeArr: [23, 24, 25],
            approveType: '',
            confirmNum: 1,
            appLyName: '',
            status: this.$route.params.status,
            errorTime: 0
        }
    },
    mounted() {
        document.setTitle('请休假');
        let vm = this;
        vm.getVuex();
        vm.initForm();
        vm.approveLog();
        if (vm.userInfo.role != 2) {
            vm.getApprover();
        }
        vm.errorTime = 0;
        vm.$dialog.loading.open('加载中...');
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            confirmHoliday: 'getConfirmHoliday'
        })
    },
    watch: {
        realStartDate: function() {
            var vm = this;
            var start = Date.parse(vm.realStartDate);
            var end = Date.parse(vm.realEndDate);
            var msNum = end - start;
            vm.daysNum = msNum / 1000 / 60 / 60 / 24;
            vm.setConfirmHoliday({
                realStartDate: vm.realStartDate
            });
        },
        realEndDate: function() {
            var vm = this;
            var start = Date.parse(vm.realStartDate);
            var end = Date.parse(vm.realEndDate);
            var msNum = end - start;
            vm.daysNum = msNum / 1000 / 60 / 60 / 24;
            vm.setConfirmHoliday({
                realEndDate: vm.realEndDate
            });
        },
        realDays: function() {
            var vm = this;
            vm.setConfirmHoliday({
                realDays: vm.realDays
            });
        },
        approver: function() {
            let vm = this;
            vm.setConfirmHoliday({
                approver: vm.approver
            });
        },
        approveType: function() {
            let vm = this;
            vm.setConfirmHoliday({
                approveType: vm.approveType
            });
        },
        appLyName: function() {
            let vm = this;
            vm.setConfirmHoliday({
                appLyName: vm.appLyName
            });
        }
    },
    methods: {
        ...mapMutations([
            'setConfirmHoliday'
        ]),
        // 获取vuex中实际休假时间
        getVuex() {
            let vm = this;
            vm.realEndDate = vm.confirmHoliday.realEndDate;
            vm.realStartDate = vm.confirmHoliday.realStartDate;
            vm.realDays = vm.confirmHoliday.realDays;
        },
        // 表单数据
        initForm() {
            var vm = this;
            vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'askforleave',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                if (res.isSuccess == 'false') {
                    vm.errorTime++;
                    if (vm.errorTime < 3) {
                        vm.initForm();
                    } else {
                        vm.$dialog.loading.close();
                        vm.$dialog.toast({
                            mes: '表单未加载成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                } else if (res.isSuccess == 'true') {
                    vm.$dialog.loading.close();
                }
                if (res.jsonData.CURRENTSTATE != 10 && res.jsonData.CURRENTSTATE != 4 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    return false;
                }
                let data = res.jsonData;
                vm.formData = data;
                vm.startDate = vm.formatDate(data.startDate, 'yyyy-MM-dd');
                vm.endDate = vm.formatDate(data.endDate, 'yyyy-MM-dd');
                vm.realStartDate = vm.formatDate(data.realStartDate, 'yyyy-MM-dd');
                vm.realEndDate = vm.formatDate(data.realEndDate, 'yyyy-MM-dd');
                vm.realDays = data.realDays;
                vm.days = data.days;
                vm.appLyName = data.appLyName;
                for (let key in data.nextBtn) {
                    if (key.indexOf('doApprove') != -1) {
                        for (let keyJ in data.nextBtn.doApprove) {
                            if (keyJ.indexOf('type') != -1) {
                                vm.approveType = data.nextBtn.doApprove[keyJ];
                            }
                        }
                    }
                }
            }, function(res) {
                if (res.response.status != 200) {
                    vm.errorTime++;
                    if (vm.errorTime < 3) {
                        vm.initForm();
                    } else {
                        vm.$dialog.loading.close();
                        vm.$dialog.toast({
                            mes: '表单未加载成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                }
            })
        },
        // 获取审批人
        getApprover() {
            var vm = this;
            for (var i = 0; i < vm.queryTypeArr.length; i++) {
                var item = vm.queryTypeArr[i];
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'askforleave',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'queryType': item
                }, function(res) {
                    var data = res.jsonData;
                    for (var j = 0; j < data.length; j++) {
                        var item2 = data[j];
                        vm.approver.push(item2);
                    }
                }, function(res) {}, false);
            }
        },
        // 审批流程
        approveLog() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApprove', {
                'type': 'askforleave',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'workItemID': vm.$route.params.workId
            }, function(res) {
                vm.aproveList = res.jsonData;
            }, function(res) {}, false)
        },
        // 确定按钮
        confHoliday() {
            let vm = this;
            if (vm.userInfo.role == 2) {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'askforleave',
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': 'finishActivity',
                    'leaderIDs': '',
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.formData.firstorgname + vm.formData.appLyName + '请假申请',
                    'ext': '{"realStartDate":"' + vm.realStartDate + '","realEndDate":"' + vm.realEndDate + '","realDays":"' + vm.realDays + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '完成确认',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            } else {
                if (vm.startDate != vm.realStartDate || vm.endDate != vm.realEndDate) {
                    vm.$dialog.toast({
                        mes: '实际休假与请假表单不符合时<br>请先送领导审批',
                        timeout: 2000
                    });
                } else if (vm.days != vm.realDays) {
                    vm.$dialog.toast({
                        mes: '实际休假与请假表单不符合时<br>请先送领导审批',
                        timeout: 2000
                    });
                } else {
                    vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                        'type': 'askforleave',
                        'workItemID': vm.$route.params.workId,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'activityID': 'finishActivity',
                        'leaderIDs': '',
                        'fuyan': '',
                        'opinion': '',
                        'workTitle': vm.formData.firstorgname + vm.formData.appLyName + '请假申请',
                        'ext': '{"realStartDate":"' + vm.realStartDate + '","realEndDate":"' + vm.realEndDate + '","realDays":"' + vm.realDays + '"}'
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成确认',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle/');
                            }
                        });
                    });
                }
            }
        },
        // 跳转到选择领导页面
        toSelectLeader() {
            let vm = this;
            vm.$router.push('/selectLeader/false/' + vm.$route.params.workId);
        }
    }
}
</script>
<style scoped>
