    margin-top: 0;
}

.holiday_two {
    border-bottom: none;
    margin: 0;
    padding-bottom: 0;
}

.holiday_two .yd-flexbox-item .yd-flexbox {
    position: relative;
}

.footer {
    /*padding: 0 0.4rem;*/
    position: fixed;
    bottom: 0;
